import React, { useRef } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import "./ChannelForm.scss"

export default function ChannelForm({state}) {
  const name = useRef();
  const description = useRef();
  const image = useRef();

  async function handleSubmit(e) {
    state.setAlert(false)
    e.preventDefault()
    if (localStorage.getItem("jwt")) {
      const formData = new FormData();
      formData.append('description', description.current.value);
      formData.append('name', name.current.value);
      formData.append('image', image.current.files[0]);
    
      try {
        const response = await fetch('http://localhost:3001/channels/createChannel', {
          method: 'POST',
          body: formData,
          headers: {
            authorization: localStorage.getItem("jwt")
          }
        });
    
        const json = await response.json();
        if (json.error) {
          state.setResponseType("danger")
          if (typeof json.error === "string") {
            state.setMessage(json.error)
          } else {
            state.setMessage("Erreur du serveur, veuillez rsayer plus tard")
          }
          state.setAlert(true)
        } else {
          state.setResponseType("success")
          state.setMessage(json.message)
          state.setAlert(true)
          name.current.value = ""
          description.current.value = ""
          image.current.value = ""
        }
      } catch (error) {
        console.error(error);
      }
    } else {
      state.setResponseType("danger")
      state.setMessage("Vous devez tre connect  un compte pour vous crer une chane")
      state.setAlert(true)
    }
  }

  return (
    <Form className='form'>
      <h3>Cration de chane CodingTub</h3>
      <Form.Group className="mb-3" controlId="form-title">
        <Form.Label>Titre de la chane</Form.Label>
        <InputGroup>
          <Form.Control
            ref={name}
            type="text"
            placeholder="Entrer le titre (min 4 caractres)"
            style={{ marginRight: "0px" }}
          />
          <InputGroup.Text onClick={() => name.current.value=""}>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-lg" viewBox="0 0 16 16">
              <path d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z"/>
            </svg>
          </InputGroup.Text>
        </InputGroup>  
        <Form.Text className="text-muted">
          Utilisez uniquement des mots corrects.
        </Form.Text>
      </Form.Group>

      <Form.Group  className="mb-3" controlId="form-description">
        <Form.Label>Description de la chane</Form.Label>
        <Form.Control
          ref={description}
          as="textarea"
          className="form-description"
          placeholder="Entrer la description (min 10 caractres)"
          style={{ width: "100%" }}
        />
        <Form.Text className="text-muted">
          Utilisez uniquement des mots corrects.
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="form-image-link">
        <Form.Label>Miniature de la chane</Form.Label>
        <InputGroup>
          <Form.Control
            ref={image}
            type="file" 
            accept="image/png, image/jpeg, image/jpg"
            placeholder="Entrer le lien de votre miniature (min 10)"
          />
          <InputGroup.Text onClick={() => image.current.value=""}>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-lg" viewBox="0 0 16 16">
              <path d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z"/>
            </svg>
          </InputGroup.Text>
        </InputGroup>
        <Form.Text className="text-muted">
          Extensions .png, .jpeg, .jpg
        </Form.Text>
      </Form.Group>

      <Button variant="primary" type="submit" onClick={(e) => handleSubmit(e)}>
        Crer la chane
      </Button>
    </Form>
  )
}
